import sess from '../../utils/sess'
import app from '../../utils/appApi'
import {MessageBox, Toast} from 'mint-ui'
function coverAuth (areaInfo) {
  let auth = {}
  areaInfo.forEach(item => {
    auth[item['FuncName']] = item['CheckColumn'] === 'True'
  })
  return auth
}
export default {
  state: {
    user: null,
    ip: '',
    auth: ''
  },
  getters: {
    ip: state => state.ip,
    auth: state => state.auth,
    user: state => {
      if (!state.user) {
        state.user = sess.get('user')
      }
      if (!state.user)  {
        return {
          UserID: 'test30',
          PositionName: '系统管理员',
          OrgName: '银河世纪花园',
          OrgID: '11091315263400010000',
          PositionID: '11109131631300010000',
          memberId: '30',
          memberName: 'test30'
        }
      }
      return state.user
    }
  },
  mutations: {
    setAuth (state, auth) {
      state.auth = auth
    },
    setIp (state, ip) {
      state.ip = ip
    },
    login (state, user) {
      state.user = user
      sess.set('user', user)
      state.isLogin = true
    },
    logout (state) {
      sess.remove('user')
      state.user = null
    }
  },
  actions: {
    async getUserAction ({getters, commit}) {
      // return Promise.resolve("{}")
      let login = await app.getUser()
      if (typeof login === 'string') {
        return Promise.reject(new Error('登录数据格式为string类型'))
      }
      let ip = login['serverAddress']
      let data = login['loginInfo']['User_LoginAppNew'][0]['Syswin'][0]
      console.log(data)
      if (data.status + '' === '1') {
        commit('login', data.data)
        commit('setIp', ip)
        commit('setAuth', coverAuth(data.data['AreaInfo']))
        Toast({
          message: '登录成功',
          position: 'bottom',
          duration: 1500
        })
        return data.data // 返回User对象　
      } else {
        console.log('网络异常，请重新登录')
        Toast({
          message: status.msg + ';返回值' + data.status,
          position: 'bottom',
          duration: 1500
        })
        return Promise.reject(new Error('网络异常，请重新登录'))
      }
    }
  }
}



